import { ToggleButton, Tooltip } from "@mui/material";
import { IAgoraRTCClient, ICameraVideoTrack } from "agora-rtc-react";
import VideoOffIcon from "assets/icons/platform/video_off.svg";
import VideoOnIcon from "assets/icons/platform/video_on.svg";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { videoService } from "services/video.service";
import { RootState, useShallowEqualSelector } from "store";
import { setShowUserCamera } from "store/video.store";
import { mute } from "../utils/mute";

const VideoControl = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const { start } = useShallowEqualSelector((state: RootState) => ({
    start: state.video.start,
  }));

  const { client, videoTrack } = videoService;
  const hasVideo = videoTrack?.enabled;
  const [videoState, setVideoState] = useState(true);

  if (!videoTrack && !start) return null;

  const videoIcon = hasVideo ? VideoOnIcon : VideoOffIcon;
  const title = hasVideo ? "turn_off_camera" : "turn_on_camera";

  const hideVideo = async () => {
    await mute(
      client as IAgoraRTCClient,
      videoTrack as ICameraVideoTrack,
      videoState,
      setVideoState,
    )();
    dispatch(setShowUserCamera(!videoState));
  }

  return (
    <ToggleButton
      onClick={hideVideo}
      value="video"
    >
      <Tooltip title={t(`controls.${title}`) as string}>
        <img alt="video" src={videoIcon} />
      </Tooltip>
    </ToggleButton>
  );
};

export default VideoControl;
