import {
  IAgoraRTCClient,
  ICameraVideoTrack,
  ILocalTrack,
  IMicrophoneAudioTrack,
} from "agora-rtc-react";

export const mute =
  (
    client: IAgoraRTCClient,
    mediaTrack: IMicrophoneAudioTrack | ICameraVideoTrack,
    mediaState: boolean,
    setMediaState: (audioState: boolean) => void,
  ) =>
  async () => {
    if (!client?.uid) return;
    const newMediaState = !mediaState;
    await mediaTrack?.setEnabled(newMediaState);
    if (newMediaState) {
      await client?.publish(mediaTrack as ILocalTrack);
    } else {
      await client?.unpublish(mediaTrack);
    }
    setMediaState(newMediaState);
  };
