import { TRANSLATION_EN } from "lang/en";
import { keys } from "lodash";
import { useTranslation } from "react-i18next";
import Styles from "./audience.module.css";
import SpaceshipImg from "assets/images/spaceship.webp";
import { Tooltip } from "@mui/material";

const Audience = () => {
  const { t } = useTranslation();
  const sections = keys(TRANSLATION_EN.landing.audience.options);

  return (
    <div>
      <h2 className={`${Styles.title} codaTitle`}>{t("landing.audience.title")}</h2>
      <h3 className={Styles.subtitle}>{t("landing.audience.subtitle")}</h3>
      <ul className={Styles.sections}>
        {sections.map((section, key) => (
          <li className={Styles.section} key={key}>
            <Tooltip
              classes={{ tooltip: Styles.tooltip }}
              title={`${t(`landing.audience.options.${section}.examples`)}`}
            >
              <div>
                <img width={50} alt="spaceship" src={SpaceshipImg} />
                <b>{t(`landing.audience.options.${section}.title`)}: </b>
                {t(`landing.audience.options.${section}.message`)}
              </div>
            </Tooltip>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Audience;
