import NeonButton from "components/neon-button/neon-button";
import NeonInput from "components/neon-input/neon-input";
import { environment } from "environment";
import { useFormik } from "formik";
import { FormikWrapper } from "hoc";
import { TFunction, useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import { post } from "utils";
import * as yup from "yup";
import Styles from "./early-access-form.module.css";

const { apiUrl } = environment;

const nameInput = (t: TFunction<"translation", undefined>) => (props: unknown) =>
  (
    <NeonInput
      data-testid="name-input"
      className={Styles.formElement}
      id="name"
      label={t("common.name")}
      {...props}
    />
  );

const emailInput = (t: TFunction<"translation", undefined>) => (props: unknown) =>
  (
    <NeonInput
      data-testid="email-input"
      className={Styles.formElement}
      id="email"
      label={t("common.email")}
      {...props}
    />
  );

const EarlyAccessForm = () => {
  const { t } = useTranslation();

  const validationSchema = yup.object({
    name: yup.string().required(t("common.name_is_required")),
    email: yup
      .string()
      .email(t("common.enter_a_valid_email"))
      .required(t("common.email_is_required")),
  });

  const formik = useFormik({
    initialValues: {
      email: "",
      name: "",
    },
    validationSchema,
    onSubmit: async (values, { resetForm }) => {
      await post(`${apiUrl}/users/preinscription`, values);
      toast.success(t("landing.thank_you_for_your_interest"));
      resetForm();
    },
  });

  return (
    <div className={`${Styles.content} column`}>
      <div className={`column ${Styles.formAlign}`}>
        <div>
          <h2 className={`${Styles.title} codaTitle`}>{t("landing.title")}</h2>
          <h3 className={`${Styles.subtitle} codaTitle`}>{t("landing.subtitle_1")}</h3>
          <form
            onSubmit={formik.handleSubmit}
            className={Styles.formContainer}
            id="register-early-access-form"
            data-testid="register-early-access-form"
          >
            <FormikWrapper formik={formik} name="name">
              {nameInput(t)}
            </FormikWrapper>
            <FormikWrapper formik={formik} name="email">
              {emailInput(t)}
            </FormikWrapper>
            <NeonButton
              id="submit-button"
              type="submit"
              className={Styles.formElement}
              label={t("landing.register_for_early_access")}
            />
          </form>
        </div>
      </div>
    </div>
  );
};

export default EarlyAccessForm;
