import { UserVideoPlayer } from "components/video-player";
import { OnlyVideoPlayer } from "components/video-players/only-video-player";
import { useMemo } from "react";
import { useShallowEqualSelector, RootState } from "store";

interface Props {
  userId: number;
  className: string;
}

export const SimpleVideoPlayer = (props: Props) => {
  const { userId, usersHash } = useShallowEqualSelector((state: RootState) => ({
    userId: state.user.id,
    usersHash: state.video.usersHash,
  }));

  return useMemo(() => {
    if (Number(userId) === Number(props.userId)) {
      return <UserVideoPlayer className={props.className} />;
    }
    return <OnlyVideoPlayer className={props.className} userId={props.userId} />;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [userId, usersHash])
};
