import NeonButton from "components/neon-button/neon-button";
import NeonInput from "components/neon-input/neon-input";
import NeonSelect from "components/neon-select/neon-select";
import { environment } from "environment";
import { useFormik } from "formik";
import { FormikWrapper } from "hoc";
import { ChildrenProps } from "hoc/formik-wrapper";
import { TRANSLATION_EN } from "lang/en";
import { keys } from "lodash";
import { useEffect } from "react";
import { TFunction, useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import { post } from "utils";
import * as yup from "yup";
import Styles from "./contact-form.module.css";

const { apiUrl } = environment;

const nameInput = (t: TFunction<"translation", undefined>) => (props: unknown) =>
  (
    <NeonInput
      data-testid="name-input"
      className={Styles.formElement}
      id="name"
      label={t("common.name")}
      {...props}
    />
  );

const emailInput = (t: TFunction<"translation", undefined>) => (props: unknown) =>
  (
    <NeonInput
      data-testid="email-input"
      className={Styles.formElement}
      id="email"
      label={t("common.email")}
      {...props}
    />
  );

const subjectInput = (t: TFunction<"translation", undefined>) => (props: ChildrenProps) => {
  const { error, value, onChange } = props;
  const options = keys(TRANSLATION_EN.landing.contact_form.subjects).map((subject: string) => ({
    value: subject,
    label: t(`landing.contact_form.subjects.${subject}.label`),
  }));
  return (
    <NeonSelect
      className={Styles.formElement}
      error={error as boolean}
      label={t("landing.contact_form.subject")}
      onChange={onChange}
      value={value}
      id="subject"
      name="subject"
      options={options}
      errorMessage={t(`landing.contact_form.subject_is_required`)}
    />
  );
};

const messageInput = (t: TFunction<"translation", undefined>) => (props: unknown) =>
  (
    <NeonInput
      data-testid="message-input"
      className={Styles.formElement}
      id="message"
      variant="outlined"
      label={t("landing.contact_form.message")}
      multiline
      rows={5}
      {...props}
    />
  );

const ContactForm = () => {
  const { t } = useTranslation();

  const validationSchema = yup.object({
    name: yup.string().required(t("common.name_is_required")),
    email: yup
      .string()
      .email(t("common.enter_a_valid_email"))
      .required(t("common.email_is_required")),
    subject: yup.string().required(t("landing.contact_form.subject_is_required")),
    message: yup.string().required(t("landing.contact_form.message_is_required")),
  });

  const formik = useFormik({
    initialValues: {
      email: "",
      name: "",
      message: "",
      subject: "",
    },
    validationSchema,
    onSubmit: async (values, { resetForm }) => {
      await post(`${apiUrl}/users/contact-form`, values);
      toast.success(t("landing.thank_you_for_your_interest"));
      resetForm();
    },
  });

  useEffect(() => {
    const { subject } = formik.values;
    if (!subject) return;
    const message = t(`landing.contact_form.subjects.${subject}.message`);
    formik.setFieldValue("message", message);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [formik.values.subject]);

  return (
    <div className={`centerContent`}>
      <div className={Styles.card}>
        <form
          onSubmit={formik.handleSubmit}
          className={Styles.formContainer}
          id="contact-form"
          data-testid="contact-form"
        >
          <label className={`codaTitle`}>{t("landing.contact_form.label")}</label>
          <div className={Styles.contactInformation}>
            <FormikWrapper formik={formik} name="name">
              {nameInput(t)}
            </FormikWrapper>
            <FormikWrapper formik={formik} name="email">
              {emailInput(t)}
            </FormikWrapper>
          </div>
          <div className={Styles.message}>
            <FormikWrapper formik={formik} name="subject">
              {subjectInput(t)}
            </FormikWrapper>
            <FormikWrapper formik={formik} name="message">
              {messageInput(t)}
            </FormikWrapper>
          </div>
          <div className="center">
            <NeonButton id="submit-button" type="submit" label={t("landing.contact_form.send")} />
          </div>
        </form>
      </div>
    </div>
  );
};

export default ContactForm;
