import { Dispatch } from "@reduxjs/toolkit";
import {
  IAgoraRTCClient,
  IAgoraRTCRemoteUser,
  ICameraVideoTrack,
  IMicrophoneAudioTrack,
} from "agora-rtc-react";
import { setUserHash, setVideoTrackReady } from "store/video.store";
import hash from "object-hash";

class VideoService {
  private _videoTrack?: ICameraVideoTrack;
  private _users: Array<IAgoraRTCRemoteUser> = [];
  public audioTrack?: IMicrophoneAudioTrack;
  public client?: IAgoraRTCClient;

  setVideoTrack(videoTrack: ICameraVideoTrack, dispatch: Dispatch) {
    this._videoTrack = videoTrack;
    dispatch(setVideoTrackReady(true));
  }

  get videoTrack() {
    return this._videoTrack;
  }

  addUser(user: IAgoraRTCRemoteUser, dispatch: Dispatch) {
    this._users.push(user);
    this.updateHash(dispatch);
  }

  removeUser(userToRemove: IAgoraRTCRemoteUser, dispatch: Dispatch) {
    this._users = this._users.filter(user => String(user.uid) !== String(userToRemove.uid));
    this.updateHash(dispatch);
  }

  updateHash(dispatch: Dispatch) {
    dispatch(setUserHash(this.getUsersHash()));
  }

  get users() {
    return this._users || [];
  }

  private getUsersHash() {
    const userUIDS = this._users.map(user => ({
      uid: user.uid,
      hasAudio: user.hasAudio && !!user.audioTrack,
      hasVideo: user.hasVideo && !!user.videoTrack,
    }));
    return hash(userUIDS);
  }
}

export const videoService = new VideoService();
