import { round } from "lodash";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Styles from "./statistic.module.css";

export interface IStatistic {
  prefix: string;
  suffix: string;
  value: number;
  link: string;
  index?: number;
}

const goToPage = (link: string) => () => {
  window.open(link, "_blank");
};

const easeOutQuad = (t: number) => t * (2 - t);

const duration = 1500;

const frameDuration = 2000 / 60;

const Statistic = (props: IStatistic) => {
  const { t } = useTranslation();
  const { prefix, suffix, value, index, link } = props;
  const [count, setCount] = useState(0);

  useEffect(() => {
    let frame = 0;
    const totalFrames = Math.round(duration / frameDuration);
    const counter = setInterval(() => {
      frame++;
      const progress = easeOutQuad(frame / totalFrames);
      setCount(value * progress);

      if (frame === totalFrames) {
        clearInterval(counter);
      }
    }, frameDuration);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div role="contentinfo" className={Styles.container} onClick={goToPage(link)}>
      <h3 className={Styles.title}>
        {prefix}
        {round(count, 2)}
        {suffix}
      </h3>
      <p className={Styles.description}>
        {t(`landing.statistics.statistics_${(index as number) + 1}`)}
      </p>
    </div>
  );
};

export default Statistic;
