import { IRemoteVideoTrack } from "agora-rtc-react";
import Video from "components/video/video";
import { useMemo } from "react";
import { videoService } from "services/video.service";
import { useShallowEqualSelector, RootState } from "store";

interface Props {
  className?: string;
  style?: React.CSSProperties;
  userId: number;
}

export const OnlyVideoPlayer = (props: Props) => {
  const { usersHash } = useShallowEqualSelector((state: RootState) => ({
    usersHash: state.video.usersHash,
  }));

  const usersWithVideo = videoService.users;
  const videoInformation = usersWithVideo.find(
    userWithVideo => Number(userWithVideo.uid) === Number(props.userId),
  );
  const videoTrack = videoInformation?.hasVideo
  ? (videoInformation.videoTrack as IRemoteVideoTrack)
  : undefined;

  return useMemo(() => {
    return (
      <Video className={props.className as string} style={props.style} videoTrack={videoTrack} />
    )
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [!!videoInformation?.hasVideo, props.className, props.style, usersHash]);
};
