import Video from "components/video/video";
import { useMemo } from "react";
import { videoService } from "services/video.service";
import { RootState, useShallowEqualSelector } from "store";
import Styles from "./user-video-player.module.css";

interface Props {
  className?: string;
  style?: React.CSSProperties,
}

export const UserVideoPlayer = (props: Props) => {
  const { videoUID, showUserCamera } = useShallowEqualSelector((state: RootState) => ({
    videoUID: state.video.uid,
    showUserCamera: state.video.showUserCamera,
  }));

  const dependencies = [
    videoUID,
    !!videoService.videoTrack?.enabled,
    showUserCamera,
  ];
  
  return useMemo(() => {
    const videoTrack = videoUID && showUserCamera ? videoService.videoTrack : undefined;
    return (
      <Video
        className={`${Styles.videoPlayer} ${props.className}`}
        style={props.style}
        cameraTrack={videoTrack}
      />
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dependencies])
}
