import { createClient, createMicrophoneAndCameraTracks } from "agora-rtc-react";
import { ClientConfig } from "agora-rtc-sdk-ng";
import { environment } from "environment";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { roomService } from "services/room.service";
import { videoService } from "services/video.service";
import { RootState, useShallowEqualSelector } from "store";
import { setStart, setUID } from "store/video.store";

interface Props {
  children?: React.ReactNode;
}

const config: ClientConfig = {
  mode: "rtc",
  codec: "h264",
};

export const VideoWrapper = (props: Props) => {
  const useClient = createClient(config);
  const useMicrophoneAndCameraTracks = createMicrophoneAndCameraTracks(
    {},
    { encoderConfig: "480p_6" },
  );

  const client = useClient();
  const { ready, tracks } = useMicrophoneAndCameraTracks();

  const { channelName, userId, start } = useShallowEqualSelector((state: RootState) => ({
    channelName: state.room.pin,
    userId: state.user.id,
    start: state.video.start,
  }));

  const dispatch = useDispatch();

  useEffect(() => {
    const init = async (name: string) => {
      videoService.client = client;
      if (tracks) {
        const [audioTrack, videoTrack] = tracks;
        videoService.audioTrack = audioTrack;
        videoService.setVideoTrack(videoTrack, dispatch);
      }
      client.on("user-published", async (user, mediaType) => {
        try {
          await client.subscribe(user, mediaType);
          if (mediaType === "video") {
            videoService.addUser(user, dispatch);
          }

          if (mediaType === "audio") {
            user.audioTrack?.play();
            videoService.updateHash(dispatch);
          }
        } catch (e) {
          console.error(e);
        }
      });

      client.on("user-unpublished", (user, type) => {
        if (type === "audio") {
          user.audioTrack?.stop();
          videoService.updateHash(dispatch);
        }

        if (type === "video") {
          user.videoTrack?.stop();
          videoService.removeUser(user, dispatch);
        }
      });

      client.on("user-left", user => {
        videoService.removeUser(user, dispatch);
      });

      const tokenReponse = await roomService.getToken(channelName);

      const token = tokenReponse.data.token;

      const {
        agora: { appId },
      } = environment;

      const uid = await client.join(appId as string, name, token, userId);

      dispatch(setUID(uid));

      if (tracks && client) {
        await client.publish([tracks[0], tracks[1]]);
      }

      dispatch(setStart(true));
    };

    if (ready && tracks && channelName && userId && !start && !videoService.client) {
      init(channelName);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [channelName, client, ready]);

  useEffect(() => {
    const listener = () => {
      window.addEventListener("online", () => {
        window.location.reload();
      });
    };
    window.addEventListener("offline", listener);

    return () => {
      if (videoService.videoTrack?.stop) {
        videoService.videoTrack.close();
        videoService.videoTrack.stop();
      }

      if (videoService.audioTrack?.stop) {
        videoService.audioTrack?.stop();
        videoService.audioTrack?.close();
      }
      if (videoService.client?.leave) {
        videoService.client?.leave();
      }
      window.removeEventListener("offline", listener);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return <>{props.children}</>;
};
