import { AgoraVideoPlayer, ICameraVideoTrack, IRemoteVideoTrack } from "agora-rtc-react";
import NoVideoPlayer from "components/video/no-video-player/no-video-player";
import { CSSProperties } from "react";
import Styles from "./video.module.css";
interface Props {
  className: string;
  style?: CSSProperties;
  videoTrack?: IRemoteVideoTrack;
  cameraTrack?: ICameraVideoTrack;
}

const hasVideo = (props: Props) => {
  const videoTrack = props.videoTrack || props.cameraTrack;
  return videoTrack;
};

const Video = (props: Props) => {
  const videoTrack = props.videoTrack || props.cameraTrack;
  if (!hasVideo(props)) {
    return <NoVideoPlayer className={props.className} style={props.style} />;
  }
  return (
    <AgoraVideoPlayer
      className={`${props.className} ${Styles.videoPlayer}`}
      style={props.style}
      videoTrack={videoTrack as IRemoteVideoTrack}
    />
  );
};

export default Video;
