import { IAgoraRTCRemoteUser, IRemoteVideoTrack } from "agora-rtc-react";
import Video from "components/video/video";
import { StoryTimeUserWrapper } from "hoc";
import Styles from "./user-video.module.css";
import { videoStyle } from "./user-video.style";

interface Props {
  usersWithVideo: Array<IAgoraRTCRemoteUser>;
  videoInformation: IAgoraRTCRemoteUser;
  storyUserIsTheSameUserInSession: boolean;
}

const UserVideo = (props: Props) => {
  const { usersWithVideo, videoInformation, storyUserIsTheSameUserInSession } = props;

  if (storyUserIsTheSameUserInSession) return null;

  const storyUserStyle = { ...videoStyle };

  if (usersWithVideo.length > 1) {
    storyUserStyle.height = "25vh";
  }

  const videoTrack = videoInformation?.hasVideo
    ? (videoInformation.videoTrack as IRemoteVideoTrack)
    : undefined;

  return (
    <StoryTimeUserWrapper isPlaying={videoInformation?.audioTrack?.isPlaying}>
      <Video className={Styles.videoPlayer} style={storyUserStyle} videoTrack={videoTrack} />
    </StoryTimeUserWrapper>
  );
};

export default UserVideo;
