import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { cloneDeep } from "lodash";
export interface VideoState {
  start: boolean;
  uid?: string;
  videoTrackReady?: boolean;
  usersHash: string;
  showUserCamera?: boolean;
}

const initialState: VideoState = {
  start: false,
  usersHash: "",
  showUserCamera: true,
};

export const videoSlice = createSlice({
  name: "video",
  initialState,
  reducers: {
    setStart: (state, action: PayloadAction<boolean>) => {
      const { payload } = action;
      state.start = payload;
    },
    setUID: (state, action: PayloadAction<number | string>) => {
      const { payload } = action;
      state.uid = String(payload);
    },
    setVideoTrackReady: (state, action: PayloadAction<boolean>) => {
      const { payload } = action;
      state.videoTrackReady = payload;
    },
    setUserHash: (state, action: PayloadAction<string>) => {
      const { payload } = action;
      state.usersHash = payload;
    },
    setShowUserCamera: (state, action: PayloadAction<boolean>) => {
      const { payload } = action;
      state.showUserCamera = payload;
    },
    cleanStore: state => {
      state = cloneDeep(initialState);
    },
  },
});

export const { setStart, setUID, setVideoTrackReady, setUserHash, setShowUserCamera, cleanStore } =
  videoSlice.actions;

export default videoSlice.reducer;
