
import Slide1 from 'assets/images/mockups/slide-1.webp';
import Slide2 from 'assets/images/mockups/slide-2.webp';
import Slide3 from 'assets/images/mockups/slide-3.webp';
import Slide4 from 'assets/images/mockups/slide-4.webp';
import OwlCarousel, { OwlCarouselProps } from 'react-owl-carousel';

const configuration: Partial<OwlCarouselProps> = {
  loop: true,
  margin: 35,
  responsive: {
    0: {
      items: 1
    }, 
    992: {
      items: 2
    }
  },
  autoplay: false,
  smartSpeed: 500,
  center: true,
  dots: true,
}

const slides = [
  Slide1,
  Slide2,
  Slide3,
  Slide4
];

const Carousel = () => {
  return (
    <OwlCarousel className='owl-theme' {...configuration} nav>
      {slides.map((slide, key) => (
        <div key={key} className='item'>
          <img src={slide} alt='' />
        </div>
      ))}
    </OwlCarousel>
  )
};

export default Carousel;
