import { Box, Tab, Tabs } from "@mui/material";
import feature1 from "assets/icons/features/feature-1.webp";
import feature10 from "assets/icons/features/feature-10.webp";
import feature11 from "assets/icons/features/feature-11.webp";
import feature12 from "assets/icons/features/feature-12.webp";
import feature13 from "assets/icons/features/feature-13.webp";
import feature14 from "assets/icons/features/feature-14.webp";
import feature15 from "assets/icons/features/feature-15.webp";
import feature16 from "assets/icons/features/feature-16.webp";
import feature17 from "assets/icons/features/feature-17.webp";
import feature18 from "assets/icons/features/feature-18.webp";
import feature2 from "assets/icons/features/feature-2.webp";
import feature3 from "assets/icons/features/feature-3.webp";
import feature4 from "assets/icons/features/feature-4.webp";
import feature5 from "assets/icons/features/feature-5.webp";
import feature6 from "assets/icons/features/feature-6.webp";
import feature7 from "assets/icons/features/feature-7.webp";
import feature8 from "assets/icons/features/feature-8.webp";
import feature9 from "assets/icons/features/feature-9.webp";
import { TRANSLATION_EN } from "lang/en";
import { keys, times } from "lodash";
import { SyntheticEvent, useState } from "react";
import { TFunction, useTranslation } from "react-i18next";
import SwipeableViews from "react-swipeable-views";
import FeatureDetail from "./feature-detail/feature-detail";
import Styles from "./features.module.css";

const featuresImages: Record<string, Array<string>> = {
  enterprises: [feature1, feature2, feature3, feature4, feature5, feature6],
  community: [feature7, feature8, feature9, feature10, feature11, feature12],
  events: [feature13, feature14, feature15, feature16, feature17, feature18],
};

const renderTab =
  (t: TFunction<"translation", undefined>, activeTab: number) =>
  (category: string, index: number) =>
    (
      <Tab
        data-testid={`tab_${category}`}
        key={`details_${category}`}
        value={index}
        label={t(`landing.features.${category}.label`)}
        className={`${Styles.title} ${activeTab === index ? Styles.titleActive : ""}`}
      />
    );

const renderDetails = (t: TFunction<"translation", undefined>) => (category: string) => {
  const categoryImages = featuresImages[category];
  const details = times(6).map((index: number) => (
    <FeatureDetail
      image={categoryImages[index]}
      title={t(`landing.features.${category}.options.option_${index + 1}.title`)}
      message={t(`landing.features.${category}.options.option_${index + 1}.message`)}
      key={index}
    ></FeatureDetail>
  ));
  
  return (
    <div
      key={`details_${category}`}
      data-testid={`details_${category}`}
      className={Styles.detailsContainer}
    >
      {details}
    </div>
  );
};

const Features = () => {
  const { t } = useTranslation();
  const featuresCategories = keys(TRANSLATION_EN.landing.features);
  const [value, setValue] = useState(0);
  const handleChange = (_event: SyntheticEvent<Element, Event>, newValue: number) => {
    setValue(newValue);
  };
  return (
    <div className={Styles.featuresContainer}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs variant="scrollable" scrollButtons="auto" value={value} onChange={handleChange}>
          {featuresCategories.map(renderTab(t, value))}
        </Tabs>
      </Box>
      <br />
      <SwipeableViews key="test" index={value}>
        {featuresCategories.map(renderDetails(t))}
      </SwipeableViews>
    </div>
  );
};

export default Features;
