import { ToggleButton, Tooltip } from "@mui/material";
import { IAgoraRTCClient, IMicrophoneAudioTrack } from "agora-rtc-react";
import MicOffIcon from "assets/icons/platform/mic_off.svg";
import MicOnIcon from "assets/icons/platform/mic_on.svg";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { videoService } from "services/video.service";
import { RootState, useShallowEqualSelector } from "store";
import { mute } from "../utils/mute";

const AudioControl = () => {
  const { t } = useTranslation();
  const { start } = useShallowEqualSelector((state: RootState) => ({
    start: state.video.start,
  }));

  const { client, audioTrack } = videoService;
  const hasAudio = audioTrack?.enabled;
  const [audioState, setAudioState] = useState(true);

  if (!audioTrack && !start) return null;

  const audioIcon = hasAudio ? MicOnIcon : MicOffIcon;

  const title = hasAudio ? "mute" : "unmute";

  return (
    <ToggleButton
      onClick={mute(
        client as IAgoraRTCClient,
        audioTrack as IMicrophoneAudioTrack,
        audioState,
        setAudioState,
      )}
      value="audio"
    >
      <Tooltip title={t(`controls.${title}`) as string}>
        <img alt="audio" src={audioIcon} />
      </Tooltip>
    </ToggleButton>
  );
};

export default AudioControl;
